
import { getCurrentInstance } from 'vue';
export default {
  name: 'Index',
  setup() {
    const instance = getCurrentInstance() as any;
    function loading() {
      instance.proxy.$loading.show('大王叫我来巡山，请稍等……');
      setTimeout(() => {
        instance.proxy.$loading.hide();
      }, 3000);
    }
    return { loading };
  }
};
